import React from 'react';
import './App.css';
import ReactDOM from 'react-dom';

const operatorPattern = '([/*+-])';
const operatorPatternWithoutSub = '([/*+])';
const subtractPattern = '([-])';
const numberPattern = '([0-9])';
/*const clickSound = 'https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3'*/

const deviceData = "CALC - POTATO EDITION"

class Calc extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentDisplay: '',
            formulaDisplay: '',
            result: '',
            lastInput: '',
        };

        this.handleClick = this.handleClick.bind(this)
    }

    calculate() {
        let formula = this.state.formulaDisplay.trim();
        let aResult = eval(formula)
        this.setState({
            currentDisplay: aResult,
            formulaDisplay: this.state.formulaDisplay + '=' + aResult,
            result: aResult,
            lastInput: '='
        })
        return aResult
    }

    resetDisplay() {
        this.setState({
            currentDisplay: '0',
            formulaDisplay: '',
            result: '',
            lastInput: '',
        })
    }
/*    playClickSound() {
        const sound = new Audio(clickSound)
        sound.play()
    }*/

    addInput(input, currentDisplay = '', formulaDisplay = '', result = '') {
        this.setState({
            currentDisplay: currentDisplay !== '' ? currentDisplay.trim() : input,
            formulaDisplay: formulaDisplay !== '' ? formulaDisplay.trim() : input,
            result: result,
            lastInput: input
        })
    }

    inputDisplayAction(input) {
        let lastInput = this.state.lastInput;
        let currentDisplay = this.state.currentDisplay;

        if (currentDisplay.length > 17) {
            return 'error'
        }
        if (input === 'AC') {
            return this.resetDisplay()
        }
        if (input === '=') {
            this.calculate()
            return ''
        }
        if (this.state.result && input.match(numberPattern)) {
            return 'simpleAdd'
        }
        if (this.state.lastInput === '=' && input.match(operatorPattern)) {
            return 'newFormulaFromResult'
        }
        if (lastInput === '0' && input === '0' && this.state.currentDisplay.length < 2) {
            return ''
        }
        if (currentDisplay.match('[.]') && input.match('[.]')) {
            return ''
        }
        if (input.match(operatorPatternWithoutSub) && this.state.currentDisplay === '0') {
            return ''
        }
        if (lastInput === '-' && input.match(operatorPattern)) {
            return 'replaceOp'
        }
        if (lastInput === '-' && input.match(operatorPattern) && this.state.currentDisplay.length < 2) {
            return ''
        }
        if (lastInput && input.match('[.]')) {
            return 'concatNums'
        }
        if (lastInput.match('[.]') && input.match(numberPattern)) {
            return 'concatNums'
        }
        if (lastInput.match(operatorPattern) && input.match(subtractPattern)) {
            return 'addDoubleOp'
        }
        if (lastInput.match(operatorPattern) && input.match(operatorPattern)) {
            return 'replaceOp'
        }
        if (lastInput.match(numberPattern) && input.match(numberPattern)) {
            return 'concatNums'
        }
        if (lastInput.match(numberPattern) && input.match(operatorPattern)) {
            return 'addOp'
        }
        if (lastInput.match(operatorPattern) && input.match(numberPattern)) {
            return 'concatNums'
        }
        return 'simpleAdd'
    }

    handleClick(event) {
/*        this.playClickSound();*/
        const input = event.target.value;
        switch (this.inputDisplayAction(input)) {
            case 'error':
                this.addInput(' ',  'LENGTH ERROR', this.state.formulaDisplay)
                break;
            case 'simpleAdd':
                this.addInput(input);
                break;
            case 'concatNums':
                this.addInput(input, this.state.currentDisplay.concat(input), this.state.formulaDisplay.concat(input))
                break;
            case 'addOp':
                this.addInput(input, ' ', this.state.formulaDisplay.concat(input))
                break;
            case 'replaceOp':
                this.addInput(input, ' ', this.state.formulaDisplay.slice(0, -1) + event.target.value)
                if (this.state.formulaDisplay.slice(0, -1).match(operatorPattern)) {
                    this.addInput(input, ' ', this.state.formulaDisplay.slice(0, -2) + event.target.value)
                }
                break;
            case 'addDoubleOp':
                this.addInput(input, ' ', this.state.formulaDisplay.concat(input))
                break;
            case 'equals':
                this.addInput(input, ' ', this.state.formulaDisplay.concat(input), this.calculate())
                break;
            case 'newFormulaFromResult':
                this.addInput(input, ' ', this.state.result + input, '')
                break;
            default:
                return ''
        }
    }

    componentDidMount() {
        this.resetDisplay()
    }

    render() {
        return (
            <div id="calc">
                <Display currentInputDisplay={this.state.currentDisplay} formulaDisplay={this.state.formulaDisplay}
                         deviceData={deviceData}/>
                <Buttons toDisplay={this.handleClick}/>
            </div>
        )
    }
}

class Display extends React.Component {
    render() {
        return (
            <div id='display-container'>
                <div id='formula'>{this.props.formulaDisplay}</div>
                <div id='display'>
                    {this.props.currentInputDisplay}
                </div>
                <div id='device-data'>{this.props.deviceData}&#8482;</div>
            </div>
        )
    }
}

class Buttons extends React.Component {
    render() {
        return (
            <div id="calc-grid">
                <div id="enter-button" className='calc-button big'>
                    <button id="equals" onClick={this.props.toDisplay} value='='>=</button>
                </div>
                <div id="ac-button" className='calc-button big'>
                    <button id="clear" onClick={this.props.toDisplay} value='AC'>AC</button>
                </div>
                <div id="zero-button" className='calc-button'>
                    <button id="zero" onClick={this.props.toDisplay} value='0'>0</button>
                </div>
                <div className='calc-button operator'>
                    <button id="divide" onClick={this.props.toDisplay} value='/'>/</button>
                </div>
                <div className='calc-button operator'>
                    <button id="multiply" onClick={this.props.toDisplay} value='*'>x</button>
                </div>
                <div className='calc-button'>
                    <button id="seven" onClick={this.props.toDisplay} value='7'>7</button>
                </div>
                <div className='calc-button'>
                    <button id="eight" onClick={this.props.toDisplay} value='8'>8</button>
                </div>
                <div className='calc-button'>
                    <button id="nine" onClick={this.props.toDisplay} value='9'>9</button>
                </div>
                <div className='calc-button operator'>
                    <button id="subtract" onClick={this.props.toDisplay} value='-'>-</button>
                </div>
                <div className='calc-button'>
                    <button id="four" onClick={this.props.toDisplay} value='4'>4</button>
                </div>
                <div className='calc-button'>
                    <button id="five" onClick={this.props.toDisplay} value='5'>5</button>
                </div>
                <div className='calc-button'>
                    <button id="six" onClick={this.props.toDisplay} value='6'>6</button>
                </div>
                <div className='calc-button operator'>
                    <button id="add" onClick={this.props.toDisplay} value='+'>+</button>
                </div>
                <div className='calc-button'>
                    <button id="one" onClick={this.props.toDisplay} value='1'>1</button>
                </div>
                <div className='calc-button'>
                    <button id="two" onClick={this.props.toDisplay} value='2'>2</button>
                </div>
                <div className='calc-button'>
                    <button id="three" onClick={this.props.toDisplay} value='3'>3</button>
                </div>
                <div className='calc-button'>
                    <button id='decimal' onClick={this.props.toDisplay} value='.'>.</button>
                </div>
            </div>
        )
    }
}

function App() {
    return (
        <div className="App">
            <Calc/>
        </div>
    );
}

ReactDOM.render(<App/>, document.getElementById('root')); // had to remove export App and replace with this one to pass the tests
